@import "./tokens.css";

/* Search highlighting styles */
.search-highlight {
  background-color: #fef3c7;
  color: #92400e;
  padding: 0.1em 0.2em;
  border-radius: 0.2em;
  font-weight: 500;
}

/* Monetary search highlighting styles */
.monetary-search .search-highlight {
  background-color: #dcfce7;
  color: #15803d;
  padding: 0.1em 0.2em;
  border-radius: 0.2em;
  font-weight: 500;
}

.monetary-search .search-context mark {
  background-color: #dcfce7;
  color: #15803d;
  padding: 0.1em 0.2em;
  border-radius: 0.2em;
  font-weight: 500;
}

/* Monetary value highlighting - light green for numeric matches */
.monetary-highlight {
  background-color: #dcfce7;
  color: #15803d;
  padding: 0.1em 0.2em;
  border-radius: 0.2em;
  font-weight: 500;
}

.search-context {
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
  margin-top: 0.25rem;
  line-height: 1.4;
}

.search-context mark {
  background-color: #fef3c7;
  color: #92400e;
  padding: 0.1em 0.2em;
  border-radius: 0.2em;
  font-weight: 500;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: var(--font-family-base);
  background: var(--color-bg);
  color: var(--color-text);
  line-height: var(--line-height-normal);
}

#app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
  display: flex;
  flex-direction: column;
}

button, input {
  font-family: inherit;
}

button {
  cursor: pointer;
  border: none;
  border-radius: var(--radius-sm);
  padding: 0.5rem 1rem;
  font-size: var(--font-size-sm);
  background: var(--color-accent);
  color: #fff;
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.app-header {
  border-bottom: 1px solid var(--color-border);
  background: var(--color-surface);
}

.app-nav {
  max-width: 1100px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  gap: var(--space-lg);
  padding: var(--space-md) var(--space-xl);
}

.brand {
  font-size: var(--font-size-lg);
  font-weight: 600;
}

.search-area {
  flex: 1;
  display: flex;
  justify-content: center;
  position: relative;
}

.search-area input[type='search'] {
  width: min(520px, 100%);
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-md);
  border: 1px solid var(--color-border);
  font-size: var(--font-size-md);
  background: var(--color-surface);
  color: var(--color-text);
}

.search-area input[type='search']:focus {
  outline: 2px solid var(--color-accent);
  outline-offset: 2px;
}

/* Monetary search mode styles */
.search-form.monetary-search {
  position: relative;
}

.search-form.monetary-search::before {
  content: "💰";
  position: absolute;
  left: var(--space-sm);
  top: 50%;
  transform: translateY(-50%);
  font-size: var(--font-size-sm);
  z-index: 1;
  pointer-events: none;
}

.search-area input[type='search'].monetary-search {
  background: linear-gradient(135deg, #f0fdf4 0%, #dcfce7 100%);
  border-color: #22c55e;
  color: #15803d;
  padding-left: calc(var(--space-sm) + 1.5rem);
}

.search-area input[type='search'].monetary-search:focus {
  outline: 2px solid #22c55e;
  outline-offset: 2px;
  background: linear-gradient(135deg, #f0fdf4 0%, #dcfce7 100%);
  border-color: #16a34a;
}

.nav-actions {
  display: flex;
  gap: var(--space-sm);
}

main {
  padding: var(--space-2xl) var(--space-xl);
}

.home-ghost {
  max-width: 760px;
  margin: 0 auto;
  padding: var(--space-xl);
  border-radius: var(--radius-md);
  border: 1px dashed var(--color-border);
  text-align: center;
  color: var(--color-text-muted);
}

.app-main {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: var(--space-2xl) var(--space-xl);
}

.screen {
  display: none;
}

.screen:not([hidden]) {
  display: block;
}

.screen--home .home-ghost {
  background: var(--color-surface);
  box-shadow: var(--shadow-soft);
}

.search-form {
  width: 100%;
}

.search-dialog-host {
  position: absolute;
  top: calc(100% + var(--space-sm));
  left: 0;
  width: min(520px, 100%);
  z-index: 10;
}

.search-dialog-host:empty {
  display: none;
}

.search-dialog {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
  background: var(--color-surface);
  border-radius: var(--radius-md);
  box-shadow: var(--shadow-popover);
  border: 1px solid var(--color-border);
  padding: var(--space-lg);
  max-height: 520px;
  overflow-y: auto;
}

.search-dialog__summary {
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
}

.search-dialog__group h4 {
  margin: 0 0 var(--space-xs);
  font-size: var(--font-size-sm);
  text-transform: uppercase;
  letter-spacing: 0.06em;
  color: var(--color-text-muted);
}

.search-dialog__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
}

.search-dialog__item {
  display: flex;
  flex-direction: column;
  gap: var(--space-2xs);
  padding-bottom: var(--space-sm);
  border-bottom: 1px solid var(--color-border);
}

.search-dialog__item:last-child {
  border-bottom: none;
  padding-bottom: 0;
}

.search-dialog__item-title {
  font-weight: 600;
}

.search-dialog__item-meta {
  font-size: var(--font-size-xs);
  color: var(--color-text-muted);
}

.search-dialog__empty,
.search-dialog__loading,
.search-dialog__error {
  text-align: center;
  color: var(--color-text-muted);
  padding: var(--space-lg);
}

.search-dialog__loading {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-md);
}

.spinner {
  width: var(--icon-size-lg);
  height: var(--icon-size-lg);
  border-radius: 50%;
  border: 3px solid var(--color-border);
  border-top-color: var(--color-accent);
  animation: spin 0.8s linear infinite;
}

.search-dialog__footer {
  position: sticky;
  bottom: calc(-1 * var(--space-lg));
  display: flex;
  justify-content: flex-end;
  background: var(--color-surface);
  background: linear-gradient(180deg, color-mix(in srgb, var(--color-surface) 20%, transparent 80%), var(--color-surface) 100%);
  padding: var(--space-md) var(--space-lg) var(--space-md) var(--space-lg);
  margin: var(--space-sm) calc(-1 * var(--space-lg)) 0 calc(-1 * var(--space-lg));
  border-top: 1px solid var(--color-border);
  box-shadow: 0 -8px 16px rgba(15, 23, 42, 0.08);
  z-index: 1;
}

.see-all-button {
  font-weight: 600;
  margin: 0;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.nav-actions button {
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  color: var(--color-text);
  font-weight: 500;
}

.nav-actions button.is-active {
  background: var(--color-accent);
  color: #fff;
  border-color: var(--color-accent);
}

button.secondary {
  background: var(--color-surface);
  color: var(--color-text);
  border: 1px solid var(--color-border);
}

.results-view {
  display: flex;
  flex-direction: column;
  gap: var(--space-xl);
  max-width: 1100px;
  margin: 0 auto;
}

.results-view__main {
  display: flex;
  gap: var(--space-xl);
  align-items: flex-start;
}

.results-view__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  gap: var(--space-lg);
}

.results-view__summary {
  margin-top: var(--space-xs);
  color: var(--color-text-muted);
}

.results-view__actions button {
  background: none;
  border: none;
  color: var(--color-accent);
  padding: 0;
}

.results-view__facets {
  width: 240px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
}

.results-view__facets.is-empty {
  color: var(--color-text-muted);
  font-size: var(--font-size-sm);
}

.results-view__facet-block {
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
}

.results-view__facet-block h3 {
  margin: 0;
  font-size: var(--font-size-sm);
  font-weight: 600;
  color: var(--color-text);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.results-view__facet-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: var(--space-2xs);
}

.results-view__facet-item {
  margin: 0;
}

.facet-checkbox {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  cursor: pointer;
  padding: var(--space-2xs) 0;
  font-size: var(--font-size-sm);
  color: var(--color-text);
}

.facet-checkbox__input {
  margin: 0;
  cursor: pointer;
}

.facet-checkbox__text {
  flex: 1;
}

.facet-checkbox__count {
  font-size: var(--font-size-xs);
  color: var(--color-text-muted);
  background: var(--color-surface);
  padding: 0 var(--space-2xs);
  border-radius: var(--radius-sm);
}

.results-view__groups {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
}

.results-list {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.results-group {
  background: var(--color-surface);
  border-radius: var(--radius-md);
  border: 1px solid var(--color-border);
  padding: var(--space-lg);
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.results-group__header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  gap: var(--space-sm);
  border-bottom: 1px solid var(--color-border);
  padding-bottom: var(--space-sm);
}

.results-group__header h2 {
  margin: 0;
}

.results-group__count {
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
}

.results-group__list {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.result-card {
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  padding: var(--space-md);
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
  background: #fff;
}

.result-card__header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  gap: var(--space-sm);
}

.result-card__badge {
  background: var(--color-accent-soft);
  color: var(--color-accent);
  border-radius: 999px;
  padding: 0 var(--space-sm);
  font-size: var(--font-size-xs);
  font-weight: 600;
}

.result-card__summary {
  margin: 0;
  color: var(--color-text-muted);
}

.result-card__meta {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-md);
  margin: 0;
  padding: 0;
}

.result-card__meta span {
  display: block;
  font-size: var(--font-size-xs);
  color: var(--color-text-muted);
}

.result-card__meta strong {
  display: block;
  margin-top: var(--space-2xs);
}

.result-card__line-items {
  border-top: 1px solid var(--color-border);
  padding-top: var(--space-sm);
}

.result-card__line-items h4 {
  margin: 0 0 var(--space-xs);
  font-size: var(--font-size-sm);
}

.line-items-toggle {
  background: none;
  border: none;
  color: var(--color-primary);
  font-size: var(--font-size-sm);
  font-weight: 500;
  cursor: pointer;
  padding: 0.25rem 0;
  margin-bottom: 0.5rem;
  text-decoration: underline;
  text-underline-offset: 0.2em;
}

.line-items-toggle:hover {
  color: var(--color-primary-dark);
  text-decoration: none;
}

.line-items-table {
  width: 100%;
  border-collapse: collapse;
  font-size: var(--font-size-sm);
  margin-top: var(--space-xs);
}

.line-items-table th {
  text-align: left;
  padding: var(--space-xs) var(--space-sm);
  background-color: var(--color-background-subtle);
  border-bottom: 1px solid var(--color-border);
  font-weight: 600;
  font-size: var(--font-size-xs);
  color: var(--color-text-muted);
  text-transform: uppercase;
  letter-spacing: 0.025em;
}

.line-items-table td {
  padding: var(--space-sm);
  border-bottom: 1px solid var(--color-border-subtle);
  vertical-align: top;
}

.line-items-table tr:last-child td {
  border-bottom: none;
}

.line-item__description {
  font-weight: 600;
  color: var(--color-text);
}

.line-item__type {
  color: var(--color-text-muted);
  font-size: var(--font-size-xs);
}

.line-item__quantity {
  color: var(--color-text-muted);
  font-size: var(--font-size-xs);
}

.line-item__unit-price {
  color: var(--color-text-muted);
  font-size: var(--font-size-xs);
  text-align: right;
}

.line-item__total {
  font-weight: 600;
  color: var(--color-text);
  text-align: right;
}

.line-item__more-row td {
  padding: var(--space-sm);
  text-align: center;
  font-style: italic;
}

.line-item__more {
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
}

/* Responsive table for smaller screens */
@media (max-width: 768px) {
  .line-items-table {
    font-size: var(--font-size-xs);
  }
  
  .line-items-table th,
  .line-items-table td {
    padding: var(--space-xs);
  }
  
  .line-item__unit-price,
  .line-item__total {
    text-align: left;
  }
}

.results-view__empty {
  text-align: center;
  color: var(--color-text-muted);
}

.settings-view {
  max-width: 720px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
}

.settings-form {
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  padding: var(--space-lg);
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
}

.settings-field {
  display: flex;
  flex-direction: column;
  gap: var(--space-2xs);
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
}

.settings-field input,
.settings-field select {
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-sm);
  border: 1px solid var(--color-border);
  font-size: var(--font-size-sm);
  background: var(--color-surface);
  color: var(--color-text);
}

.settings-field select {
  cursor: pointer;
}

.settings-group {
  border: none;
  margin: 0;
  padding: 0;
}

.settings-group legend {
  font-size: var(--font-size-sm);
  font-weight: 600;
  color: var(--color-text);
  margin-bottom: var(--space-md);
  padding: 0;
  border: none;
}

.settings-group__grid {
  display: grid;
  gap: var(--space-sm);
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
}

.settings-actions {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-sm);
}

@media (max-width: 768px) {
  .app-nav {
    padding: var(--space-md);
    flex-direction: column;
    align-items: stretch;
  }

  .search-area {
    width: 100%;
  }

  .nav-actions {
    justify-content: flex-end;
  }

  .app-main {
    padding: var(--space-lg);
  }

  .results-view__main {
    flex-direction: column;
  }

  .results-view__facets {
    width: 100%;
    order: 2;
  }

  .results-view__groups {
    order: 1;
  }
}

/* Mini line items in search dialog */
.mini-line-items {
  margin-top: var(--space-xs);
  display: block;
}

.mini-line-items__table {
  width: 100%;
  border-collapse: collapse;
  font-size: var(--font-size-xs);
  color: var(--color-text-muted);
}

.mini-line-items__table td {
  padding: 0.125rem 0.25rem;
  border-bottom: 1px solid var(--color-border-subtle);
  vertical-align: top;
  line-height: 1.3;
}

.mini-line-items__table tr:last-child td {
  border-bottom: none;
}

.mini-line-items__description {
  font-weight: 500;
  color: var(--color-text);
  max-width: 120px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.mini-line-items__type {
  font-size: calc(var(--font-size-xs) - 1px);
  color: var(--color-text-muted);
  max-width: 60px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.mini-line-items__quantity {
  font-size: calc(var(--font-size-xs) - 1px);
  color: var(--color-text-muted);
  text-align: center;
  max-width: 50px;
}

.mini-line-items__unit-price {
  font-size: calc(var(--font-size-xs) - 1px);
  color: var(--color-text-muted);
  text-align: right;
  max-width: 60px;
}

.mini-line-items__total {
  font-weight: 500;
  color: var(--color-text);
  text-align: right;
  max-width: 60px;
}

.mini-line-items__more-row td {
  padding: 0.125rem 0.25rem;
  text-align: center;
  font-style: italic;
  font-size: calc(var(--font-size-xs) - 1px);
  color: var(--color-text-muted);
}

.mini-line-items__more {
  font-size: calc(var(--font-size-xs) - 1px);
  color: var(--color-text-muted);
}

/* Make mini line items even more compact in search dialog */
@media (max-width: 768px) {
  .mini-line-items__table {
    font-size: calc(var(--font-size-xs) - 1px);
  }
  
  .mini-line-items__table td {
    padding: 0.1rem 0.2rem;
  }
  
  .mini-line-items__description {
    max-width: 100px;
  }
  
  .mini-line-items__type {
    max-width: 50px;
  }
  
  .mini-line-items__quantity {
    max-width: 40px;
  }
  
  .mini-line-items__unit-price {
    max-width: 50px;
  }
  
  .mini-line-items__total {
    max-width: 50px;
  }
}
